import "./index.scss";

import React from "react";

import toolsPng from "../../../../assets/raster/tools.png";
import detailsIcon from "../../../../assets/vector/details-icon.svg";
import downloadAndPrintIcon from "../../../../assets/vector/download-and-print-icon.svg";
import templateIcon from "../../../../assets/vector/template-icon.svg";
import { Ellipse } from "../../../atoms";
import Stage from "./Stage";

const DevelopmentStages = () => (
  <section className="development-stages">
    <div className="development-stages--container">
      <Ellipse animate additionalStyle={{ top: 500, left: 300, width: 500, height: 300 }} isLight />
      <Ellipse
        animate
        additionalStyle={{ top: 300, left: 1200, width: 900, height: 400 }}
        isTurquoise
      />
      <h2 className="development-stages--heading">
        <img src={toolsPng} alt="tools icon" /> Development Stages
      </h2>
      <p className="development-stages--text">
        As a startup, we are going through the development cycle.
      </p>
      <div className="development-stages--stages">
        <Stage label={"Choose your template"} icon={templateIcon} arrow={"right"} />
        <Stage label={"Enter the details"} icon={detailsIcon} arrow={"right"} />
        <Stage label={"Download and Print"} icon={downloadAndPrintIcon} arrow={"down"} />
        <Stage label={"Download and Print"} icon={downloadAndPrintIcon} arrow={"left"} />
        <Stage label={"Enter the details"} icon={detailsIcon} arrow={"left"} />
        <Stage label={"Choose your template"} icon={templateIcon} />
      </div>
    </div>
  </section>
);

export default DevelopmentStages;
