import "./index.scss";

import React from "react";

const Legend = ({ items }) => {
  return (
    <div className="legend">
      {items.map(({ value, label, color }) => (
        <div className="legend--item">
          <div className="legend--value" style={{ color }}>
            {value}
          </div>
          <div className="legend--label">{label}</div>
        </div>
      ))}
    </div>
  );
};

export default Legend;
