import "./index.scss";

import React from "react";

import { PieChart } from "react-minimal-pie-chart";

import Legend from "./Legend";
import LineLabel from "./LineLabel";
import { useWindowDimensions } from "../../../../helpers/hooks";

const TokenDistribution = () => {
  const { width } = useWindowDimensions();
  return (
    <section className="token-distribution">
      <div className="token-distribution--container">
        <h2 className="token-distribution--heading">Unilaw token distribution</h2>
        <div className="token-distribution--row">
          <div className="token-distribution--chart">
            <PieChart
              startAngle={-90}
              label={(label) => width < 768 && label.dataEntry.value}
              labelStyle={{ fontSize: 8, fill: "#ffffff" }}
              labelPosition={70}
              data={[
                { color: "#5861D6", value: 43.5, title: "Circulating Supply" },
                { color: "#5861D6C7", value: 15, title: "Strategic development" },
                { color: "#5861D6D4", value: 10, title: "Marketing" },
                { color: "#5861D68A", value: 7.5, title: "Community (incl. Airdrop)" },
                { color: "#5861D6B2", value: 7.5, title: "Brand / streaming partnership" },
                { color: "#5861D6A3", value: 5, title: "Advisory" },
                { color: "#5861D6D1", value: 5, title: "Liquidity" },
                { color: "#5652C5", value: 5, title: "Team" },
              ]}
            />
            {width >= 768 && (
              <>
                <LineLabel label={"5%"} p1={{ x: -2.04, y: -2.04 }} p2={{ x: 20.4, y: 20.4 }} side={"left"} />
                <LineLabel label={"5%"} p1={{ x: 15.7, y: -17.96 }} p2={{ x: 31.02, y: 12.65 }} side={"left"} />
                <LineLabel label={"5%"} p1={{ x: 38.16, y: -30.08 }} p2={{ x: 44.28, y: 8.57 }} side={"left"} />
                <LineLabel label={"7.5%"} p1={{ x: -23.87, y: 24.08 }} p2={{ x: 11.22, y: 34.69 }} side={"left"} />
                <LineLabel label={"7.5%"} p1={{ x: -27.95, y: 58.16 }} p2={{ x: 8.98, y: 54.1 }} side={"left"} />
                <LineLabel label={"10%"} p1={{ x: -5.51, y: 89.8 }} p2={{ x: 17.76, y: 74.49 }} side={"left"} />
                <LineLabel label={"15%"} p1={{ x: 38.77, y: 124.49 }} p2={{ x: 44.29, y: 89.8 }} side={"left"} />
                <LineLabel label={"43.5%"} p1={{ x: 109.18, y: 6.12 }} p2={{ x: 81.63, y: 48.98 }} side={"right"} />
              </>
            )}
          </div>
          <Legend
            items={[
              { value: " 43,5%", label: "Circulating Supply", color: "#5861D6" },
              { value: "15%", label: "Strategic development", color: "#5861D6C7" },
              { value: "10%", label: "Marketing", color: "#5861D6D4" },
              { value: "7,5%", label: "Community\n(incl. Airdrop)", color: "#5861D68A" },
              { value: "7,5%", label: "Brand / streaming\npartnership", color: "#5861D6B2" },
              { value: "5%", label: "Advisory", color: "#5861D6A3" },
              { value: "5%", label: "Liquidity", color: "#5861D6D1" },
              { value: "5%", label: "Team", color: "#5652C5" },
              { value: "56,5%", label: "Total Non-Circulating Supply" },
            ]}
          />
        </div>
      </div>
    </section>
  );
};

export default TokenDistribution;
