import "./index.scss";

import React, { useMemo } from "react";

const LineLabel = ({ label, side, p1, p2 }) => {
  const length = useMemo(() => Math.sqrt((p1.x - p2.x) ** 2 + (p1.y - p2.y) ** 2), [p1, p2]);
  const angle = useMemo(() => Math.atan2(p2.y - p1.y, p2.x - p1.x), [p1, p2, length]);

  return (
    <div>
      <div
        className="line-label--line"
        style={{
          top: p1.y + "%",
          left: p1.x + "%",
          transform: `translateY(${side === "right" ? 1 : -1}px) rotate(${angle}rad)`,
          width: length + "%",
          height: 1,
        }}
      />
      <div
        className="line-label--line"
        style={{
          top: p1.y + "%",
          left: p1.x + "%",
          transform: `rotate(${side === "right" ? 0 : 180}deg)`,
          width: 30,
          height: 1,
        }}
      />
      <div
        className="line-label--circle"
        style={{
          top: `calc(${p1.y}% + ${side === "right" ? -5 : -6}px`,
          left: `calc(${p1.x}% + ${side === "right" ? 24 : -36}px`,
          width: 10,
          height: 10,
          borderRadius: 6,
        }}
      >
        <span
          className="line-label--label"
          style={
            side === "right"
              ? {
                  top: -110 + "%",
                  left: 20,
                }
              : {
                  top: -110 + "%",
                  right: 20,
                }
          }
        >
          {label}
        </span>
      </div>
    </div>
  );
};

export default LineLabel;
