import React from "react"

import "./index.scss"

import Forbes from "../../../../assets/raster/Forbes.png"
import Laptop from "../../../../assets/raster/Laptop.png"
import SpidersWeb from "../../../../assets/raster/SpidersWeb.png"
import TVN from "../../../../assets/raster/Tvn.png"
import Wyborcza from "../../../../assets/raster/Wyborcza.png"
import Paper from "../../../../assets/vector/Paper.svg"
import Plane from "../../../../assets/vector/Plane.svg"
import Ellipse from "../../../atoms/Ellipse"

const Mainview = () => {
  return (
    <div className="hero-ico">
      <div className="hero-ico--left-column">
        <div>
          <h1 className="hero-ico--heading">
            Lorem<span class="hero-ico--purple"> ipsum dolor </span>Lorem ipsum
          </h1>
          <ul className="hero-ico--list">
            <li>✅ Lorem ipsum</li>
            <li>✅ Dolor amet ipsum dolor</li>
            <li>
              ✅ Ipsum dolor amet ipsum dolor ipsum dolor amet ipsum dolor
            </li>
          </ul>
          <div className="hero-ico--buttons">
            <button className="hero-ico--button">
              DOWNLOAD WHITEPAPER
              <img
                className="hero-ico--icon"
                src={Paper}
                alt="Sheet icon"
              ></img>
            </button>
            <button className="hero-ico--button hero-ico--button__last">
              JOIN CROWDSALE
              <img
                className="hero-ico--icon"
                src={Plane}
                alt="Paper plane icon"
              ></img>
            </button>
          </div>
        </div>
        <div className="hero-ico--companies">
          <img
            alt={"Forbes logo"}
            className="hero-ico--logo hero-ico--logo__forbes"
            src={Forbes}
          ></img>
          <img
            alt={"Tvn logo"}
            className="hero-ico--logo hero-ico--logo__tvn"
            src={TVN}
          ></img>
          <img
            alt={"Wyborcza logo"}
            className="hero-ico--logo hero-ico--logo__wyborcza"
            src={Wyborcza}
          ></img>
          <img
            alt={"Spiders Web logo"}
            className="hero-ico--logo hero-ico--logo__spiders-web"
            src={SpidersWeb}
          ></img>
        </div>
      </div>
      <div className="hero-ico--right-column">
        <img alt={"laptop"} className="hero-ico--laptop" src={Laptop} />
        <Ellipse
          animate
          additionalStyle={{ top: 30, left: 50, width: 140, height: 50 }}
        />
        <Ellipse
          animate
          additionalStyle={{ top: 60, left: 150, width: 340, height: 120 }}
          isTurquoise
        />
      </div>
    </div>
  )
}

export default Mainview
