import "./index.scss";

import React from "react";

const TextLoop = ({ text, textStyle, containerStyle }) => {
  return (
    <div style={{ position: "relative", width: 10000, ...containerStyle }}>
      <span
        className="text-separator left central-side"
        style={{
          position: "absolute",
          top: 0,
          ...textStyle,
          left: 0,
        }}
      >
        {text}&nbsp;&nbsp;&nbsp;{text}&nbsp;&nbsp;&nbsp;{text}&nbsp;&nbsp;&nbsp;
      </span>

      <span
        className="text-separator left left-side"
        style={{
          position: "absolute",
          top: 0,
          ...textStyle,
          left: -textStyle.width,
        }}
      >
        {text}&nbsp;&nbsp;&nbsp;{text}&nbsp;&nbsp;&nbsp;{text}&nbsp;&nbsp;&nbsp;
      </span>

      <span
        className="text-separator left right-side"
        style={{
          position: "absolute",
          top: 0,
          ...textStyle,
          left: textStyle.width,
        }}
      >
        {text}&nbsp;&nbsp;&nbsp;{text}&nbsp;&nbsp;&nbsp;{text}&nbsp;&nbsp;&nbsp;
      </span>
    </div>
  );
};

export default TextLoop;
