import './IcoPage.scss';

import React from 'react';

import IcoNewsletterForm from '../../organisms/IcoNewsletterForm';
import NavigationIco from '../../organisms/NavigationIco';
import { useWindowDimensions } from '../../../helpers/hooks';
import BlackFooter from './BlackFooter';
import DevelopmentStages from './DevelopmentStages';
import Enter from './Enter';
import HeroIco from './HeroIco';
import LaunchpadPhase from './LaunchpadPhase';
import ManageYourLaw from './ManageYourLaw';
import PhotoSection from './PhotoSection';
import TeamSection from './TeamSection';
import TextLoop from './TextLoop';
import TokenDistribution from './TokenDistribution';
import Tokenomics from './Tokenomics';
import WhyUnilaw from './WhyUnilaw';

const IcoPage = () => {
  const { width } = useWindowDimensions();

  return (
    <>
      <NavigationIco />
      <HeroIco />
      <LaunchpadPhase />

      <Enter />
      <PhotoSection />

      <DevelopmentStages />
      <TextLoop
        text={"INCLUDING THE EARLY BIRD BONUS"}
        containerStyle={{ height: width < 768 ? 150 : 230 }}
        textStyle={
          width < 768
            ? {
                width: 2202,
                marginTop: 50,
                fontSize: 40 + "px",
                lineHeight: 55 + "px",
                fontWeight: "bold",
                fontFamily: "Inter",
              }
            : {
                width: 3852,
                marginTop: 70,
                fontSize: 70 + "px",
                lineHeight: 90 + "px",
                fontWeight: "bold",
                fontFamily: "Inter",
              }
        }
      />
      <WhyUnilaw />

      <ManageYourLaw />

      <TokenDistribution />
      <TeamSection />
      <Tokenomics />
      <TextLoop
        text={"INCLUDING THE EARLY BIRD BONUS"}
        containerStyle={{ height: width < 768 ? 150 : 230 }}
        textStyle={
          width < 768
            ? {
                width: 2202,
                marginTop: 50,
                fontSize: 40 + "px",
                lineHeight: 55 + "px",
                fontWeight: "bold",
                fontFamily: "Inter",
              }
            : {
                width: 3852,
                marginTop: 70,
                fontSize: 70 + "px",
                lineHeight: 90 + "px",
                fontWeight: "bold",
                fontFamily: "Inter",
              }
        }
      />

      <IcoNewsletterForm
        text={"Dołącz do naszego newslettera!"}
        label={"Twój email"}
        placeholder={"Email"}
        buttonText={"Dołącz"}
      />

      <BlackFooter />
    </>
  );
};

export default IcoPage;
