import "./index.scss";

import React from "react";

import { LogoText, RegularText } from "../../../atoms";
import { FooterLinks, FooterMedia, GetInTouch } from "../../../organisms";
import NebucodeLogo from '../../../../assets/vector/nebulogo.svg';

const BlackFooter = () => {
  const aboutItems = [
    { text: "O nas", link: "" },
    { text: "Zespół", link: "" },
    { text: "Współpraca", link: "" },
    { text: "Regulamin", link: "" },
    { text: "Polityka prywatności", link: "" },
    { text: "Ustawienia ciasteczek", link: "" },
  ];
  const workItems = [
    { text: "O nas", link: "" },
    { text: "Zespół", link: "" },
    { text: "Kontakt", link: "" },
  ];

  return (
    <footer className="footer footer__black">
      <div className="footer--container">
        <div className="footer-logo--wrapper">
          <LogoText isWhite={true} />
          <div className="footer-logo-text">
            <RegularText
              text={"Znamy odpowiedzi na Twoje pytania. Szybka pomoc specjalisty jakiego szukasz"}
              additionalStyle={{ color: "#FAFAFA" }}
            />
          </div>
        </div>

        <div className="get">
          <div className="footer-info--wrapper">
            <GetInTouch />
          </div>
        </div>

        <div className="footer-info-about">
          <FooterLinks title={"Kim jesteśmy"} items={aboutItems} />
        </div>

        <div className="footer-info-work">
          <FooterLinks title={"Co robimy"} items={workItems} />
        </div>

        <div className="footer-info-media">
          <FooterMedia />
        </div>

        <div className="footer-media-nebucode">
          <div className="footer-media-nebucode--copyright">
            <RegularText text={"Copyright © Unilaw 2021"} />
          </div>

          <div className="footer-media-nebucode--made-by">
            <RegularText text={"Made with"} />
            <img className={"footer-media-nebucode--logo"} src={NebucodeLogo} alt="Nebucode logo"></img>
            <RegularText text={"by Nebucode"} />
          </div>
        </div>
      </div>
    </footer>
  );
};

export default BlackFooter;
