import "./index.scss";

import React from "react";

import { cn } from "../../../../../helpers/utils";
import chevronIcon from "../../../../../assets/vector/chevron-right.svg";

const Stage = ({ icon, label, className, arrow }) => {
  return (
    <div className={cn("stage", className, arrow)}>
      <div className="stage--circle">
        <img src={icon} alt="icon" className="stage--icon" />
      </div>
      <p className="stage--label">{label}</p>
      {arrow && (
        <div className={"stage--arrow"}>
          <img src={chevronIcon} alt={`arrow ${arrow}`} />
        </div>
      )}
    </div>
  );
};

export default Stage;
