import React from "react";
import "./index.scss";

const TokenomicsItem = ({ icon, heading, text }) => {
  return (
    <div className="tokenomics-item">
      <img src={icon} alt="icon" className="tokenomics-item--icon"/>
      <h3 className="tokenomics-item--heading">{heading}</h3>
      <p className="tokenomics-item--text">{text}</p>
    </div>
  );
};

export default TokenomicsItem;
