import React from "react";
import "./index.scss";
const PriceBar = ({ max, current, leftLabel, rightLabel }) => {
  return (
    <div className="price-bar">
      <span className="price-bar--left-label">{leftLabel}</span>
      <div className="price-bar--bar">
        <div className="price-bar--inner">
          <div className="price-bar--current" style={{ width: current / max * 100 + "%" }} />
        </div>
      </div>
      <span className="price-bar--right-label">{rightLabel}</span>
    </div>
  );
};

export default PriceBar;
