import "./index.scss";

import React from "react";

import burnIcon from "../../../../assets/vector/burn-icon.svg";
import deflationIcon from "../../../../assets/vector/deflation-icon.svg";
import feeIcon from "../../../../assets/vector/fee-icon.svg";
import priceIcon from "../../../../assets/vector/price-icon.svg";
import supplyIcon from "../../../../assets/vector/supply-icon.svg";
import utilityIcon from "../../../../assets/vector/utility-icon.svg";
import Ellipse from "../../../atoms/Ellipse";
import TokenomicsItem from "./TokenomicsItem";

const Tokenomics = () => (
  <section className="tokenomics">
    <h2 className="tokenomics--heading">Tokenomics</h2>
    <div className="tokenomics--container">
      <TokenomicsItem icon={deflationIcon} heading={"deflation"} text={"Lorem ipsum dolor amet"} />
      <TokenomicsItem icon={feeIcon} heading={"1% fee"} text={"Lorem ipsum dolor amet"} />
      <TokenomicsItem icon={burnIcon} heading={"burn"} text={"Lorem ipsum dolor amet"} />
      <TokenomicsItem icon={supplyIcon} heading={"supply"} text={"Lorem ipsum dolor amet"} />
      <TokenomicsItem icon={priceIcon} heading={"price"} text={"Lorem ipsum dolor amet"} />
      <TokenomicsItem icon={utilityIcon} heading={"utility"} text={"Lorem ipsum dolor amet"} />
      <Ellipse animate additionalStyle={{ top: 200, left: 300, width: 200, height: 100 }} />
      <Ellipse animate additionalStyle={{ top: 500, left: 1000, width: 200, height: 100 }} />
      <Ellipse animate additionalStyle={{ top: 300, left: 1400, width: 250, height: 150 }} isTurquoise />
      <Ellipse animate additionalStyle={{ top: 600, left: 400, width: 400, height: 200 }} isTurquoise />
    </div>
  </section>
);

export default Tokenomics;
