import React from "react";

import "./index.scss";

import phoneImage1 from "../../../../assets/raster/phone1.png";
import phoneImage2 from "../../../../assets/raster/phone2.png";

const Law = () => {
  return (
    <section className="manage-your-law">
      <div className="manage-your-law--container">
        <div className="manage-your-law--text">
          <h2 className="manage-your-law--heading">Manage your law from your mobile</h2>
          <p className="manage-your-law--description">
            Download the app to manage your law project, keep track of the progress and complete
            tasks without procastinating. Stay on track and complete on time!
          </p>
        </div>
        <img className="manage-your-law--first-image" alt={"screen aplikacji"} src={phoneImage1} />
        <img className="manage-your-law--second-image" alt={"screen aplikacji"} src={phoneImage2} />
      </div>
    </section>
  );
};

export default Law;
