import "./index.scss";

import React from "react";

import { Button, Ellipse, Input } from "../../atoms";

const IcoNewsletterForm = ({ label, text, placeholder, buttonText }) => {
  return (
    <div className="ico-newsletter-form">
      <div className="ico-newsletter-form--container">
        <h2 className="ico-newsletter-form--heading">{text}</h2>
        <div className="ico-newsletter-form--form">
          <Input placeholder={placeholder} />
          <Button className={"ico-newsletter-form--button"} text={buttonText} />
        </div>
        <Ellipse animate additionalStyle={{ top: 200, left: 300, width: 200, height: 100 }} />
        <Ellipse animate additionalStyle={{ top: 500, left: 1000, width: 200, height: 100 }} />
        <Ellipse
          animate
          additionalStyle={{ top: 300, left: 1400, width: 250, height: 150 }}
          isTurquoise
        />
        <Ellipse
          animate
          additionalStyle={{ top: 600, left: 400, width: 400, height: 200 }}
          isTurquoise
        />
      </div>
    </div>
  );
};

export default IcoNewsletterForm;
