import "./index.scss"

import React from "react"

import Ellipse from "../../../../atoms/Ellipse"

const LaunchpadPhaseBox = ({ heading, children }) => {
  return (
    <div className="launchpad-phase-box">
      <h2 className="launchpad-phase-box--heading">{heading}</h2>
      <div className="launchpad-phase-box--box">
        <Ellipse animate additionalStyle={{top: 30, left: 50, width: 140, height: 50}}/>
        <Ellipse animate additionalStyle={{top: 60, left: 150, width: 340, height: 120}} isTurquoise/>
        {children}
      </div>
    </div>
  );
};

export default LaunchpadPhaseBox;
