import "./index.scss";

import React from "react";

import arrowIcon from "../../../../assets/vector/arrow-right.svg";
import TextLoop from "../TextLoop";
import LaunchpadPhaseBox from "./LaunchpadPhaseBox";
import PriceBar from "./PriceBar";

const LaunchpadPhase = () => {
  return (
    <section className="launchpad-phase">
      <div className="launchpad-phase--container">
        <LaunchpadPhaseBox heading={"LAUNCHPAD PHASE 1 IS LIVE"}>
          <PriceBar current={70} max={100} leftLabel={"0"} rightLabel={"100k USD"} />
          <div className="launchpad-phase--price">
            Price of <span>0.0113 USD</span> per <span>1 UNI</span>
          </div>
          <div className="launchpad-phase--raised">
            <div className="">
              69,722 <span>USD</span>
            </div>
            <div>RAISED</div>
          </div>
        </LaunchpadPhaseBox>
        <LaunchpadPhaseBox heading={"PHASE 1 TOKEN PRICING"}>
          <div className="launchpad-phase--row">
            <div className="launchpad-phase--buy">
              <span>BUY</span>
              <div>
                50,00 <span>USD</span>
              </div>
            </div>
            <img src={arrowIcon} alt="Arrow right" className="launchpad-phase--arrow" />
            <div className="launchpad-phase--buy">
              <span>BUY</span>
              <div>
                1700,00 <span>UNI</span>
              </div>
            </div>
          </div>
          <TextLoop
            text={"INCLUDING THE EARLY BIRD BONUS            "}
            textStyle={{ fontSize: 14 + "px", lineHeight: 14 + "px", width: 858 }}
            containerStyle={{ height: 20 }}
          />
        </LaunchpadPhaseBox>
      </div>
    </section>
  );
};

export default LaunchpadPhase;
