import "./index.scss";

import React, { useMemo } from "react";

import { Logo } from "../../molecules";
import { RightNav } from "..";
import { useWindowDimensions } from "../../../helpers/hooks";
import { cn } from "../../../helpers/utils";

const NavigationIco = ({ isHeaderVisible }) => {
  const { width } = useWindowDimensions();
  const condition = useMemo(
    () => !isHeaderVisible || width < 1190 || window.location.pathname !== "/",
    [isHeaderVisible, width]
  );

  return (
    <header
      className={cn("navigation", "navigation__ico")}
      style={{
        position: condition ? "fixed" : "absolute",
      }}
    >
      <div
        className={"navigation--background"}
        style={{
          display: condition ? "block" : "none",
        }}
      />
      <nav className="navigation--nav">
        <div className="navigation-logo">
          <Logo isWhite={!condition} withoutText />
        </div>
        <RightNav isDark={condition} />
      </nav>
    </header>
  );
};

export default NavigationIco;
